import 'babel-polyfill'
import Vue from 'vue'
import App from './App'
import router from './router'
import fastclick from 'fastclick'
import axios from 'axios'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import { Notification } from 'element-ui';


import 'common/stylus/index.styl'

import store from './store/index'

import VueLazyLoad from 'vue-lazyload'

Vue.use(ElementUI);

Vue.config.productionTip = false

// 
axios.interceptors.request.use((request) => {
  // console.warn('==',request.url)
  return request
}, (error) => {
  alert(`${error.config.url}${error}`)


  // Notification.error({
  //   title: '',
  //   message: `${error.config.url}${error}`,
  //   duration: 6000,
  // });
  // console.log(error.config.url, '')
  return Promise.resolve(error);
});


// 
axios
  .interceptors
  .response
  .use((response) => {
  // alert('==chenggong'+response);
  // console.warn('1==',response.request.responseURL)
    // `${daZhuanpanApi}/v1/award/export`
    // 
    // if (response.config.url !== `${daZhuanpanApi}/v1/award/export` && response.config.url !== `${daZhuanpanApi}/v1/matchaward/export` && response.config.url !== 'http://up.qiniu.com') {
    //   // errno0
    //   if (parseInt(response.data.errno, 10) !== 0) {
    //     console.log(response, 'response');
    //     Notification.error({
    //       title: '',
    //       message: `${response.config.url}:${response.data.errmsg},errno:${response.data.errno}`,
    //       duration: 5000,
    //     });
    //   }
    // }
    return response;
  }, (error) => {
  // alert(''+error)
    if (!error.response) {
      Notification.error({
        title: '',
        message: `${error.config.url}${error}`,
        duration: 5000,
      });
      // Notification.error({
      //   title: '',
      //   message: `${error.config.url}${error}`,
      //   duration: 6000,
      // });
      // console.log(error.config.url, '')
      return Promise.resolve(error);
    }
    switch (error.response.status) {
      case 400:
        Notification.error({
          title: '',
          message: `${error.response.status}:${error.response.data}`,
          duration: 5000,
        });
        break;
      case 401:
        Notification.error({
          title: '',
          message: '401',
          duration: 5000,
        });
        break;
      case 403:
        Notification.error({
          title: '',
          message: '403',
          duration: 5000,
        });
        break;
      case 404:
        console.log('40444444444444');
        alert('404')
        Notification.error({
          title: '',
          message: '404',
          duration: 5000,
        });
        break;
      case 500:
        Notification.error({
          title: '',
          message: '500',
          duration: 5000,
        });
        break;
      case 501:
        Notification.error({
          title: '',
          message: '()501',
          duration: 5000,
        });
        break;
      case 502:
        Notification.error({
          title: '',
          message: '502',
          duration: 5000,
        });
        break;
      case 503:
        Notification.error({
          title: '',
          message: '()503',
          duration: 5000,
        });
        break;
      case 504:
        Notification.error({
          title: '',
          message: '504',
          duration: 5000,
        });
        break;
      case 505:
        Notification.error({
          title: '',
          message: 'HTTP 505',
          duration: 5000,
        });
        break;
      default:
        Notification.error({
          title: '',
          message: `:${error.response.status}: ${error.response.data} `,
          duration: 5000,
        });
        return Promise.reject(error);
    }

    alert(''+error);
    console.log(':',error);
    return error;
  });

fastclick.attach(document.body)

Vue.use(VueLazyLoad, {
  loading: require('common/image/loading.svg')
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  axios,
  render: h => h(App)
})



